import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
	selector: 'app-custom-open',
	templateUrl: './custom-open.component.html',
	styleUrls: ['./custom-open.component.scss'],
	providers: [ Commfunc]
})
export class CustomOpen implements OnInit {
	show: number = 1;
	constructor(public commfunc: Commfunc) { }

	ngOnInit() {
	
		let url = window.location.href
		if (url.includes('/pages/tzb/custom/custom-marketing/cus-open/production')) {
			this.show = 1;
		} else if (url.includes('/pages/tzb/custom/custom-marketing/cus-open/nonProduction')) {
			this.show = 2;
		} else if (url.includes('/pages/tzb/custom/custom-marketing/cus-open/corporate-customer')) {
			this.show = 3;
		} else if (url.includes('/pages/tzb/custom/custom-marketing/cus-open/administrative')) {
			this.show = 4;
		}
	}
	open(num) {
		this.show = num;
	}
	 //按钮权限
	 permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
}
