import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomOpen } from './custom-open.component';//客户开立
import { CorporateCustomer } from './corporate-customer/corporate-customer.component';//企业客户
import { Administrative } from './administrative/administrative.component';//行政事业单位用户
import { Production } from './production/production.component';//生产经营性个人客户
import { NonProduction } from './non-production/non-production.component';//非生产经营性个人客户
/**
 * 客户开立
 * 
 *
 */
export const routes: Routes = [
    {
        path: '',
        component: CustomOpen,
        children: [
            { path: '', redirectTo: 'production', component: Production },
            { path: 'production', component: Production },
            { path: 'corporate-customer', component: CorporateCustomer },
            { path: 'administrative', component: Administrative },
            { path: 'nonProduction', component: NonProduction },
        ]
    }
]

export const CusOpenRouting: ModuleWithProviders = RouterModule.forChild(routes);
